// Sidebar.tsx
import { useState } from "react";

export default function Sidebar() {
  const [isSidebarOpen, setSidebarOpen] = useState(true);

  return (
    <div
      className={`bg-white shadow-lg transform transition-all duration-300 ${isSidebarOpen ? "w-64" : "w-16"}`}
    >
      <div className="h-full flex flex-col items-center md:items-start p-4">
        <button
          onClick={() => setSidebarOpen(!isSidebarOpen)}
          className="text-gray-600 hover:text-gray-900 mb-4"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M4 6h16M4 12h16m-7 6h7"
            />
          </svg>
        </button>
        {/* Sidebar Content */}
        <ul className={`space-y-4 w-full ${isSidebarOpen ? "pl-2" : "flex flex-col items-center"}`}>
          <li className="flex items-center space-x-2">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 text-gray-600"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M3 10h11M9 21v-7m0-7h3M3 3l18 18"
              />
            </svg>
            {isSidebarOpen && <span className="text-gray-700">Dashboard</span>}
          </li>
          {/* Add more sidebar items here */}
        </ul>
      </div>
    </div>
  );
}
